<template>
    <div :class="'wide-file-loader' + (inline ? ' inline' : '')">
        <div class="input-wrapper">
            <input ref="input"
                   :type="useUrl ? 'url' : 'file'"
                   :accept="accept"
                   v-model="url"
                   :placeholder="placeholder"
            />
        </div>
        <div class="buttons">
            <button @click="useUrl = !useUrl">{{ useUrl ? "使用文件" : "使用URL" }}</button>
            <button v-if="button" @click="load" :disabled="useUrl && !url && !placeholder">确定</button>
        </div>
    </div>
</template>

<script>
    import { readText} from "@/util/file";
    import * as axios from "axios";

    export default {
        name: "WideFileLoader",

        props: {
            disabled: {
                type: Boolean,
                default: false,
            },
            json: {
                type: Boolean,
                default: false,
            },
            accept: {
                type: String,
                default: '*/*',
            },
            inline: {
                type: Boolean,
                default: false,
            },
            button: {
                type: Boolean,
                default: true,
            },
            placeholder: {
                type: String,
                default: '',
            },
        },

        data() {
            return {
                useUrl: false,
                url: '',
            };
        },

        methods: {
            load() {
                (this.useUrl ? this.loadUrl : this.loadFile)();
            },
            loadFile() {
                const file = this.$refs.input.files[0];
                if (!file) {
                    this.$emit('error', '还未输入文件');
                }
                readText(file, (result, err) => this.processData(result, err));
            },
            loadUrl() {
                axios.get(this.url || this.placeholder)
                .then(response => this.processData(response.data))
                .catch(error => this.processData(null, error));
            },
            processData(raw, err) {
                if (err) {
                    this.$emit('error', err);
                } else {
                    if (this.json) {
                        if (typeof raw === 'object') {
                            this.$emit('load', raw);
                        } else {
                            try {
                                const o = JSON.parse(raw);
                                if (o) {
                                    this.$emit('load', o);
                                } else {
                                    this.$emit('error', '解析JSON失败');
                                }
                            } catch (e) {
                                this.$emit('error', e);
                            }
                        }
                    } else {
                        if (typeof raw === 'object') {
                            this.$emit('load', JSON.stringify(raw));
                        } else {
                            this.$emit('load', raw);
                        }
                    }
                }
            },

        },
    }
</script>

<style scoped>
    .wide-file-loader {
        width: 100%;
        padding: 1em;
        box-sizing: border-box;
    }

    .inline {
        display: flex;
        flex-direction: row;
    }

    .inline:first-child {
        flex: 1;
    }

    .input-wrapper {
        width: 100%;
        height: 2em;
        margin: 0 0 1em;
        padding: 0 1.6em;
        box-sizing: border-box;
    }

    input {
        width: 100%;
        box-sizing: border-box;
    }

    .buttons {
        width: 100%;
        box-sizing: border-box;

        display: flex;
        flex-direction: row;
        align-content: space-between;
    }

    .buttons > button {
        flex: 1;
        margin: 0 2em;
    }
</style>