<template>
    <div class="full-wrapper displayer">
        <div class="full-wrapper background-wrapper">
            <transition
                    enter-active-class="animate__animated animate__fadeIn animate__fast"
                    leave-active-class="animate__animated animate__fadeOut animate__fast">
                <img :key="background" class="background" :src="background" :alt="currentNode.background"/>
            </transition>
            <transition
                    enter-active-class="animate__animated animate__slideInLeft animate__faster"
                    leave-active-class="animate__animated animate__slideOutRight animate__faster">
                <img v-if="currentNode.role !== 'self'" :key="role" class="role" :src="role" :alt="currentNode.role"/>
            </transition>
        </div>
        <div class="full-wrapper content">
            <div class="scroll-wrapper option-list-wrapper">
                <transition
                        mode="out-in"
                        enter-active-class="animate__animated animate__fadeIn animate__faster"
                        leave-active-class="animate__animated animate__slideOutRight animate__faster">
                    <div :key="current" class="option-list">
                        <div v-for="(option, index) of options"
                             :key="index"
                             @click="jump(option.target)"
                             class="option-text"
                        >{{ option.text }}</div>
                    </div>
                </transition>
            </div>
            <div class="node-text-wrapper">
                <transition
                        mode="out-in"
                        enter-active-class="animate__animated animate__fadeIn animate__faster"
                        leave-active-class="animate__animated animate__fadeOut animate__faster">
                    <p class="node-text" :key="current">{{ currentNode.text }}</p>
                </transition>
            </div>
        </div>
    </div>
</template>

<script>
    import 'animate.css';

    export default {
        name: "Displayer",
        components: {},

        props: {
            script: {
                type: Object,
                required: true,
            },
        },

        data() {
            return {
                current: null,
                background: null,
                role: null,
                roleName: '',
            };
        },

        computed: {
            currentNode() {
                return this.script.nodes[this.current] || {text: '空节点', role: '', background: ''};
            },

            options() {
                const cn = this.currentNode;
                return this.currentNode.options || [{text: '...', target: cn.target}];
            },
        },

        methods: {
            jump(target) {
                const pn = this.currentNode;
                if ('number' === typeof target) {
                    this.current = target;
                } else  {
                    this.current++;
                }
                const cn = this.currentNode;
                if (pn && cn) {
                    if (cn.background && pn.background !== cn.background) {
                        this.background = this.script.resources[cn.background];
                    }
                    if (cn.role === 'self') {
                        this.role = null;
                        this.roleName = '自己';
                    } else if (cn.role && pn.role !== cn.role) {
                        const role = this.script.roles[cn.role];
                        this.role = this.script.resources[role.pic];
                        this.roleName = role.name;
                    }
                }
            },

            log(msg) {
                console.log(msg);
            },
        },

        mounted() {
            this.jump(0);
        },
    }
</script>

<style scoped>

    .displayer {
        position: relative;
        overflow: hidden;
    }

    .background-wrapper {
        position: absolute;
        z-index: -1;
        background-color: #2c3e50;
    }

    .background {
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: 50% 50%;
    }

    .role {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: 50% 50%;
    }

    .content {
        padding: 1em;
        box-sizing: border-box;

        display: flex;
        flex-direction: column-reverse;
    }

    .node-text-wrapper {
        position: static;
        margin: 0 0 .5em 0;
        padding: .5em 1.5em;
        min-height: 1em;
        max-height: 20%;
        border-top-left-radius: .5em;
        border-top-right-radius: .5em;

        font-family: 微软雅黑, sans-serif;
        background-color: rgba(255, 255, 255, 0.5);
    }

    .node-text {
        position: relative;
        margin: 0;
        padding: 0;
    }

    .option-list-wrapper {
        height: 30%;
        border-bottom-left-radius: .5em;
        border-bottom-right-radius: .5em;
        overflow-x: hidden;

        background-color: rgba(255, 255, 255, 0.5);
    }

    .option-list {
        width: 100%;
        display: flex;
        flex-direction: column;
        margin: 1em 0;
    }

    .option-text {
        margin: .2em 2em;
    }

    .option-text:hover {
        margin: 0;
        padding: .2em 2em;
        background-color: rgba(255, 255, 255, 0.5);
    }
</style>