const OBJECT = (o) => 'object' === typeof o && !Array.isArray(o);

const ARRAY = (a) => Array.isArray(a);

const NIL = (t) => t === null || t === undefined;

const STRING = (s) => 'string' === typeof s;

const NUMBER = (n) =>'number' === typeof n;

function or(...vtrs) {
    return (t, o) => vtrs.some(v => v(t, o));
}

function and(...vtrs) {
    return (t, o) => vtrs.every(v => v(t, o));
}

function object(vtrs) {
    return (t, o) => {
        if (!OBJECT(t)) {
            return false;
        }
        return Object.keys(vtrs).every(k => vtrs[k](t[k], o));
    };
}

function objectValue(vtr) {
    return (t, o) => {
        if (!OBJECT(t)) {
            return false;
        }
        return Object.values(t).every(v => vtr(v, o));
    };
}

function array(vtr) {
    return (t, o) => {
        if (!ARRAY(t)) {
            return false;
        }
        return t.every(e => vtr(e, o));
    };
}

export {
    OBJECT,
    ARRAY,
    NIL,
    STRING,
    NUMBER,
    or,
    and,
    object,
    objectValue,
    array,
}