<template>
    <div class="full-wrapper game-loader-wrapper">
        <div class="game-loader shadow">
            <div class="line">
                <span class="title">输入脚本文件</span>
                <span class="subtitle" v-if="selectedScript">输入成功【{{ scriptName }} v{{ scriptVersion }}】</span>
                <WideFileLoader accept=".json" :json="true" placeholder="/test/script.json" @error="handleError" @load="selectScript"/>
            </div>
            <div class="divider"/>
            <div class="line">
                <span class="title">输入存档文件</span>
                <span class="subtitle">留空则会生成新的存档</span>
                <span class="subtitle" v-if="selectedBundle">输入成功√</span>
                <WideFileLoader accept=".json" :json="true" @error="handleError" @load="selectBundle"/>
            </div>
            <div class="divider"/>
            <div class="line tool-bar">
<!--                <button @click="useTestScript">使用示范脚本</button>-->
<!--                <button @click="load">载入</button>-->
                <button @click="start">开始</button>
            </div>
        </div>
    </div>
</template>

<script>
    import {validateScript} from "@/structure";
    import {readJSON} from "@/util/file";
    import TEST_SCRIPT from "@/test/testScript";
    import WideFileLoader from "@/components/WideFileLoader";

    export default {
        name: "GameLoader",
        components: {WideFileLoader},
        data() {
            return {
                selectedScript: false,
                scriptName: null,
                scriptVersion: null,
                selectedBundle: false,
            }
        },

        methods: {
            handleError(error) {
                alert(error);
            },

            useTestScript() {
                this.selectScript(TEST_SCRIPT);
            },

            selectScript(obj) {
                if (!validateScript(obj) && !confirm('该脚本不符合规范，是否继续？')) {
                    return;
                }
                this.selectedScript = true;
                this.scriptName = obj.name;
                this.scriptVersion = obj.version;
                this.$emit('load-script', obj);
            },

            selectBundle(obj) {
                this.selectedBundle = true;
                this.$emit('load-bundle', obj);
            },

            load() {
                const scriptFile = this.$refs.scriptInput.files[0];
                const bundleFile = this.$refs.bundleInput.files[0];
                readJSON(scriptFile, (obj, err) => {
                    if (!err) {
                        this.selectScript(obj)
                    } else {
                        alert(err);
                    }
                });
                readJSON(bundleFile, (obj, err) => {
                    if (!err) {
                        this.selectBundle(obj)
                    } else {
                        alert(err);
                    }
                });
            },

            start() {
                if (!this.selectedScript) {
                    alert('还未载入脚本！');
                    return;
                } else if (!this.selectedBundle) {
                    alert('还未载入存档，将会自动生成存档。');
                    this.selectBundle({current: 0});
                }
                this.$emit('start-game');
            },
        },

    }
</script>

<style scoped>
    .game-loader-wrapper {
        text-align: center;
    }

    .game-loader {
        width: 100%;
        max-width: 50em;
        height: 100%;
        margin: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .line {
        width: 100%;
        margin: 1em auto;
        text-align: left;
    }

    .tool-bar {
        text-align: center;
    }

    .line > button {
        margin: 0 1em;
    }

    .title {
        margin-right: 1em;
        margin-left: 1em;
        text-align: left;
        font-size: 1.2em;
        font-weight: bold;
    }

    .subtitle {
        text-align: left;
        font-size: 1em;
        color: grey;
    }

    .divider {
        width: 100%;
        max-width: 50em;

        border-top: .1em solid lightgray;
    }
</style>