
import {array, NIL, NUMBER, object, objectValue, or, STRING} from "@/util/validate";

const SCRIPT_TYPE_CHECKER = object({
    name: STRING,
    version: STRING,
    resources: objectValue(STRING),
    roles: objectValue(object({
        name: STRING,
        pic: STRING,
    })),
    nodes: array(object({
        text: STRING,
        target: or(NIL, NUMBER),
        background: STRING,
        role: STRING,
        options: or(NIL, array(object({
            text: STRING,
            target: or(NIL, NUMBER),
        }))),
    })),
});

const SCRIPT_RELATION_CHECKER = object({
    roles: objectValue(object({
        pic: (p, s) => !!s.resources[p],
    })),
    nodes: array(object({
        role: or(NIL, (r, s) => !!s.roles[r]),
        background: or(NIL, (b, s) => !!s.resources[b]),
    })),
});

function validateScript(s) {
    return true || SCRIPT_TYPE_CHECKER(s) && SCRIPT_RELATION_CHECKER(s);
}

export {
    validateScript,
}