function readJSON(file, callbackFn) {
    if (!file) {
        return null;
    }
    const reader = new FileReader();
    reader.onload = () => {
        try {
            const o = JSON.parse(reader.result);
            callbackFn(o)
        } catch (e) {
            callbackFn(null, '读取失败，可能不是JSON格式文件！');
        }
    };
    reader.onerror = () => callbackFn(null, reader.error);
    reader.readAsText(file);
}


function readText(file, callbackFn) {
    if (!file) {
        return null;
    }
    const reader = new FileReader();
    reader.onload = () => callbackFn(reader.result);
    reader.onerror = () => callbackFn(null, reader.error);
    reader.readAsText(file);
}

export {
    readJSON,
    readText,
}